.container {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-gap: 10px;
}

.buttons-div * {
  display: flex;
  flex-direction: column;
  align-items: left;
}

.info-div {
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  font-weight: bold;
  margin-left: 15px;
  text-align: right;
  align-items: flex-end;
  color: black;
  margin-right: 10px;
}

.moves {
  /* box-sizing: border-box; */
  display: inline-block;
  text-align: center;
  align-self: left;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.App {
  text-align: center;
}

.buttons {
  width: 270px;
  background-color: white;
  opacity: 0.7;
}

.buttons > * {
  font-size: 22px;
  margin: 10px;
  width: 200px;
  /* background-color: white; */
  font-weight: bold;
  text-align: left;
}

.btn-txt-div > * {
  font-size: 1rem;
  text-align: left;
  display: flex;
  flex-direction: column;
}


.btn-txt {
  margin-left: 16px;
  font-weight: normal;
  font-size: 1.2rem;
  color:black;
  margin-top: -10px;
}

.input {
  width: 50px;
  font-size: 1.2rem;
  height: 27px;
  margin-top: -10px;
}

