.highscores {
  color: white;
  font-size: larger;
  /* background-image: url("https://images.app.goo.gl/87JMuyATUGcjJWhs7"); */
  margin: auto;
  margin-top: 20px;

  padding: 25px;
  width: 20%;
  height: 20px;
  border-collapse: collapse;
  /* border: 1px solid red; */
}

.highscores > * {
  color:black;
  font-size: larger;
  text-align: left;
  border-bottom: 1px transparent;
  opacity:1;
}

.highscores > * > * {
  padding-left: 10px;
}

.Tilt {
